import * as React from 'react';
import { connect } from 'react-redux';

import CreateList from './list';

import './index.css';

interface IProps {
    series: any
};

interface IState {
    episodes: any[]
};

class EpisodesContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            episodes: []
        };
    };
    public renderList() {
        const list: any[]=[];
        
        Object.keys(this.props.series.episodes)
            .forEach((element: string, index: number) => {
                list.push(
                    <CreateList key={`arraySeason${index+1}`}  index={index+1} />
                ); 
            });

        return list;
    };
    public render() {
        return(
            <div className="series-episode-container">
                <h2>PICK UR EPISODE</h2>
                <ul className="series-season__list">
                    {this.renderList()}
                </ul>
            </div>
        );
    };
};

const mapStateToProps = (state: any) => {
    return {
        series: state.series
    };
};

export default connect(mapStateToProps)(EpisodesContainer);


